      "galit\n\ngalit is co-authored with Vlada Milovskaya, Leila Alieva, Ignat Khlobystin and is an audio-performance based on the first part of Olga Jitlina story Adar. It describes the encounter and the experience of unintended kinship in distress with a homeless person. The addressee of the story is twofold: it is both, the person left out in a cold with no place to go, and a lover pulling away. In a sense, this is a letter addressed to a homeless person, yet the one who speaks does so from a placeless (homeless) position also. Like a hobo, the protagonist wanders around the city, at times falling into a despair, at othersinto blissful exaltation, either taking a position of power, or losing ground.",
